<template>
	<div>
		<form class="stack-small" @submit.prevent="onSubmit">
			<div>
				<label class="edit-label">Edit Name for "{{label}}"</label>
				<input :id="id" type="text" v-model.lazy.trim="newLabel">
			</div>
			<div>
				<button class="btn" type="button" @click="cancelSubmit">
					Cancel
				</button>
				<button class="btn btn__primary" type="submit">
					Save
				</button>
			</div>
		</form>
	</div>
</template>

<script>
	export default{
		props:{
			id:{required: true, type: String},
			label:{required: true, type: String}
		},
		data(){
			return{
				newLabel: this.label
			}
		},
		methods:{
			onSubmit(){
				this.$emit("labelChange", this.newLabel);
			},
			cancelSubmit(){
				this.$emit("notChange");
			}
		}
	}
</script>

<style scoped>
.edit-label {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #0b0c0c;
  display: block;
  margin-bottom: 5px;
}
input {
  display: inline-block;
  margin-top: 0.4rem;
  width: 100%;
  min-height: 4.4rem;
  padding: 0.4rem 0.8rem;
  border: 2px solid #565656;
}
form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
form > * {
  flex: 0 0 100%;
}
</style>