<template>
	<div>
		<form @submit.prevent="onSubmit">
			<label for="NewTodoInput">New Todo</label>
			<input type="text" id="NewTodoInput" autocomplete="off" v-model.lazy.trim="label" />
			<button type="submit" class="btn btn__primary btn__lg">
				Add
			</button>
		</form>
	</div>
</template>

<script>
	export default{
		methods:{
			onSubmit(){
				if(this.label != ''){
					//console.log('sucess', this.label);
					this.$emit("todoEdit", this.label); //children->parent
					this.label = '';
				}	
			}
		},
		data(){
			return{
				label: ""
			};
		}
	}

</script>